*{
  margin: 0;
  padding: 0;
}

body{
  font-family: 'Nunito Sans', sans-serif;
}

.container{
  width: 1200px;
  margin: 50px auto 0px auto;
}

.conteudo1{
  width: 500px;
}

@media(max-width:768px){
  .container{
    display: flex;
    flex-direction: column;
    max-width: 350px;
    margin: 30px auto 0px auto;
  }

  .conteudo1{
    width: 350px;
  }

  #img{
    width: 350px;
    margin-top: 30px;
  }
}

.opct{
  transition: opacity 0.3s;
}

.opct:hover{
  opacity: 0.7;
  cursor:pointer
}

.topoAtivo{
  color: rgb(1, 135, 98);
}

.rodapeAtivo{
  font-weight: bold;
}



